import React from 'react';
import makeStyles from '@material-ui/core/styles/makeStyles';
/* eslint-disable jsx-a11y/anchor-is-valid */
import Link from '@material-ui/core/Link';
import Typography from '@material-ui/core/Typography';
import SvgIcon from '@material-ui/core/SvgIcon';
import Divider from '@material-ui/core/Divider';
import axios from 'axios';
import Skeleton from '@material-ui/lab/Skeleton';
import LinearProgress from '@material-ui/core/LinearProgress';
import QRCode from 'qrcode-react';
import Grid from '@material-ui/core/Grid';
import getConfig from 'lib/getConfig';

function MobileDownload(props) {
	const {
		onFinished,
		goBack,
		messageUser,
		...rest
	} = props;
	const classes = makeStyles((theme) => ({
		root: {
			display: 'flex',
			flexDirection: 'column',
			justifyContent: 'center',
			alignItems: 'center',
		},
		link: {
			'& > * + *': {
	      marginLeft: theme.spacing(2),
	    },
		},
	}))();
	const [loading, setLoading] = React.useState(false);
	const [QRCodes, setQRCodes] = React.useState(undefined);

	function onClick(event) {
		event.preventDefault();

		const hasSubscription = sessionStorage.getItem('subscription');
		const hasSubscriptionID = sessionStorage.getItem('subscriptionID');
    if (!hasSubscription || !hasSubscriptionID) {
    	return messageUser('please make a payment', 'warn');
    }

		setLoading(true);
		function onSuccess(response) {
			const privateKeys = JSON.parse(sessionStorage.getItem('privateKeys'));
			const publicKeys = JSON.parse(sessionStorage.getItem('publicKeys'));
			const configs = response.data.map(data => {
				const filename = data.filename;
				const privateKey = publicKeys[0][filename] === data.publicKey
												 ? privateKeys[0][filename]
												 : privateKeys[1][filename];
				const config = data.config.replace('REPLACE_ME', privateKey);
				return {filename, config};
			});
			setQRCodes(
				<Grid
				  container
				  direction="row"
				  justify="space-between"
				  alignItems="center"
				  spacing={4}
				>
					{	
						configs.map(config => {
							return (
								<Grid item xs={12} sm={6} md={4}>
									<Typography>{config.filename}</Typography>
									<QRCode value={config.config} />
								</Grid>
							)
						})
					}
				</Grid>
			)
			setLoading(false);
		}
		function onError(error) {
			console.error(error);
		}
		getConfig(onSuccess, onError);
	}

	return (
		<div className={classes.root}>
			<Link
		    className={classes.link}
	    	href=""
	    	download={process.env.REACT_APP_RADAR_CONFIG_URL}
	    	onClick={onClick}
	    	variant="body2"
	    	target="_blank"
	    	rel="noreferrer"
	  	>
	  		<Typography>
	    		Scan QR code(s) below
		    </Typography>
	    </Link>
	    {loading ? <Skeleton height="200px" width="150px" /> : null}
	    {QRCodes}
		</div>
	)
}

function DesktopDownload(props) {
	const {
		onFinished,
		goBack,
		messageUser,
		...rest
	} = props;
	const classes = makeStyles((theme) => ({
		link: {
			'& > * + *': {
	      marginLeft: theme.spacing(2),
	    },
		},
	}))();
	const [loading, setLoading] = React.useState(false);

	function onClick(event) {
		event.preventDefault();

		const hasSubscription = sessionStorage.getItem('subscription');
		const hasSubscriptionID = sessionStorage.getItem('subscriptionID');
    if (!hasSubscription || !hasSubscriptionID) {
    	return messageUser('please make a payment', 'warn');
    }

		setLoading(true);
		function onSuccess(response) {
			const privateKeys = JSON.parse(sessionStorage.getItem('privateKeys'));
			const publicKeys = JSON.parse(sessionStorage.getItem('publicKeys'));
			const files = response.data.map(data => {
				const filename = data.filename;
				const privateKey = publicKeys[0][filename] === data.publicKey
												 ? privateKeys[0][filename]
												 : privateKeys[1][filename];
				const config = data.config.replace('REPLACE_ME', privateKey);
				return {filename, config};
			});
			files.forEach(file => {
				const filename = file.filename + '.conf';
			  const url = window.URL.createObjectURL(new Blob([file.config]));
			  const link = document.createElement('a');
			  link.href = url;
			  link.setAttribute('download', filename);
			  document.body.appendChild(link);
			  link.click();
			});
		  setLoading(false);
		  onFinished();
		}
		function onError(error) {
			console.error(error);
		}
		getConfig(onSuccess, onError);
	}

	return (
		<div>
	    <Link
		    className={classes.link}
	    	href=""
	    	download={process.env.REACT_APP_RADAR_CONFIG_URL}
	    	onClick={onClick}
	    	variant="body2"
	    	target="_blank"
	    	rel="noreferrer"
	  	>
	  		<Typography>
	    	Download config file(s)
		    </Typography>
	    </Link>
	    {loading ? <LinearProgress color="primary" /> : null}
		</div>
	)
}

export default function Setup(props) {
	const {
		onFinished,
		goBack,
		messageUser,
		...rest
	} = props;
  const classes = makeStyles((theme) => ({
		root: {
			width: '100%',
			height: '100%',
			display: 'flex',
			flexDirection: 'column',
			justifyContent: 'center',
			alignItems: 'center',
		},
		divider: {
			width: '45%',
			height: '0.25%',
		},
	}))();

	function onStartedDownload(id) {
	  console.log(`Started downloading: ${id}`);
	}

	function onFailed(error) {
	  console.log(`Download failed: ${error}`);
	}

  return (
  	<div className={classes.root}>
			<DesktopDownload onFinished={onFinished} goBack={goBack} messageUser={messageUser} />
			<Divider
		    className={classes.divider}
	    	orientation="horizontal"
	    	variant="middle"
    	/>
    	<Typography>
	    	or
	    </Typography>
	    <Divider
		    className={classes.divider}
	    	orientation="horizontal"
	    	variant="middle"
    	/>
			<MobileDownload onFinished={onFinished} goBack={goBack} messageUser={messageUser} />
			<hr />
			<div>
	    	<Typography variant="body2" align="center">
		    	please be patient
		    </Typography>
		    <Typography variant="body2" align="center">
		    	this takes about 60 seconds
		    </Typography>
    </div>
    </div>
  );
}
