import React from 'react';
import ReactDOM from 'react-dom';
import Skeleton from '@material-ui/lab/Skeleton';
import Divider from '@material-ui/core/Divider';
import Typography from '@material-ui/core/Typography';
import publicKeySignup from 'lib/publicKeySignup';
import LinearProgress from '@material-ui/core/LinearProgress';
import wechat_payment_qrcode from "./wechat_payment_qrcode_anonymized.png";
import alipay_payment_qrcode from "./alipay_payment_qrcode_anonymized.png";
import {makeStyles} from '@material-ui/core/styles';

function loadScript(intent, vault=false, callback) {
  const paypalScript = document.createElement('script');
  paypalScript.id = `paypal-${intent}-jssdk`;;
  paypalScript.setAttribute('async', true);
  paypalScript.setAttribute('defer', true);
  paypalScript.setAttribute('crossorigin','anonymous');
  const clientId = process.env.REACT_APP_RADAR_PAYPAL_CLIENTID;
  paypalScript.src = `https://www.paypal.com/sdk/js?client-id=${clientId}&vault=${vault}&intent=${intent}`;
  if (callback) paypalScript.onload = callback;
  const app = document.getElementById('root');
  app.parentNode.insertBefore(paypalScript, app);
}

// window.onload(() => loadScript('capture'));
window.onload(() => loadScript('subscription', true));

function Loading() {
  return (
    <React.Fragment>
      <Skeleton height="60%" width="75%" />
    </React.Fragment>
  )
}

function OnetimePayment(props) {
  const {
    goBack,
    loadingButton,
    saving,
    ...rest
  } = props;

  const classes = makeStyles((theme) => ({
    qrcodes: {
      display: 'flex',
      flexDirection: 'row',
      justifyContent: 'space-evenly',
    },
    qrcode: {
      width: '20%',
      border: '1px solid black',
    },
  }))();

  return (
    <React.Fragment>
      <Typography align="center">One Month Non-Recurring Payment</Typography>
      <div className={classes.qrcodes}>
        <img className={classes.qrcode} src={wechat_payment_qrcode} />
        <img className={classes.qrcode} src={alipay_payment_qrcode} />
      </div>
      <Typography align="center">a member of our team will</Typography>
      <Typography align="center">contact you within 24 hours</Typography>
    </React.Fragment>
  );
}

function SubscriptionPayment(props) {
  const {
    goBack,
    loadingButton,
    saving,
    ...rest
  } = props;

  const classes = makeStyles((theme) => ({
    buttons: {
      display: 'flex',
      flexDirection: 'column',
      justifyContent: 'center',
      alignItems: 'center',
      height: '20%',
    },
    paypal: {
      display: 'flex',
      flexDirection: 'column',
      justifyContent: 'center',
      width: '75%',
    },
  }))();

  return (
    <React.Fragment>
      <Typography align="center">Monthly Recurring Payment</Typography>
      <div className={classes.buttons}>
        {loadingButton ? <Loading /> : null}
        {saving ? <LinearProgress /> : null}
        <div className={classes.paypal} id="paypal-subscription_payment-button-container"></div>
      </div>
    </React.Fragment>
  );
}

export default function Payment(props) {
  const {
    onFinished,
    goBack,
    ...rest
  } = props;
  const [loadingButton, setLoadingButton] = React.useState(false);
  const [saving, setSaving] = React.useState(false);

  function loadButtons() {
    window.paypal.getFundingSources().forEach(function(fundingSource) {
      const button = window.paypal.Buttons({fundingSource: fundingSource});
      const paypalButton = document.getElementById('paypal-button-container');
      if (paypalButton && button.isEligible()) button.render(paypalButton);
    });
    setLoadingButton(false);
  }

  function renderStandardPaymentButtons() {
    if ('paypal' in window) {
      const button = window.paypal.Buttons({
        fundingSource: window.paypal.FUNDING.PAYPAL,
        createOrder: (data, actions) => {
          return actions.order.create({
            purchase_units: [{
              amount: {
                value: '100.00'
              }
            }]
          });
        },
        onApprovekey: (data, actions) => {
          sessionStorage.setItem('payment_provider', 'paypal');
          sessionStorage.setItem('subscriptionID', data.subscriptionID);
          return actions.order.capture().then(function(details) {
            console.log('Transaction completed by ' + details.payer.name.given_name);
            const onSuccess = () => {
              setSaving(false);
              onFinished();
            }
            const onError = error => {
              setSaving(false);
              console.error(error.response);
              onFinished();
            }
            setSaving(true);
            publicKeySignup(onSuccess, onError);
          });
        },
      });
      const paypalButton = document.getElementById('paypal-onetime_payment-button-container');
      if (paypalButton && button.isEligible()) {
        button.render(paypalButton);
        setLoadingButton(false);
      }
    } else {
      console.error('paypal capture script not loading');
    }
  }

  function renderSubscriptionPaymentButtons() {
    if ('paypal' in window) {
      const button = window.paypal.Buttons({
        fundingSource: window.paypal.FUNDING.PAYPAL,
        createSubscription: function(data, actions) {
          const subscription = sessionStorage.getItem('subscription');
          if (subscription.toLowerCase() === 'organization') {
            return actions.subscription.create({
              plan_id: process.env.REACT_APP_RADAR_PAYPAL_ORGANIZATION_PLANID,
              quantity: sessionStorage.getItem('quantity'),
            });
          } else if (subscription.toLowerCase() === 'single') {
            return actions.subscription.create({
              plan_id: process.env.REACT_APP_RADAR_PAYPAL_SINGLE_PLANID,
            });
          } else {
             return actions.subscription.create({
              plan_id: process.env.REACT_APP_RADAR_PAYPAL_FRIENDS_AND_FAMILY_PLANID,
            });
          }
        },
        onApprove: function(data, actions) {
          sessionStorage.setItem('payment_provider', 'paypal');
          sessionStorage.setItem('subscriptionID', data.subscriptionID);
          console.log('You have successfully created subscription ' + data.subscriptionID);
          const onSuccess = () => {
            setSaving(false);
            onFinished();
          }
          const onError = error => {
            setSaving(false);
            console.error(error.response);
            onFinished();
          }
          setSaving(true);
          publicKeySignup(onSuccess, onError);
        },
      });
      const paypalButton = document.getElementById('paypal-subscription_payment-button-container');
      if (paypalButton && button.isEligible()) {
        button.render(paypalButton);
        setLoadingButton(false);
      }
    } else {
      console.error('paypal subscription script not loading');
    }
  }

  React.useEffect(() => {
    if (sessionStorage.getItem('subscriptionID')) onFinished();
    if (!sessionStorage.getItem('subscription')) goBack();
    if (!('paypal' in window)) {
      setLoadingButton(true);
      setTimeout(() => {
        renderSubscriptionPaymentButtons();
      }, 3000);
    } else {
      renderSubscriptionPaymentButtons();
    }
  }, []);

  return (
    <React.Fragment>
      <SubscriptionPayment loadingButton={loadingButton} saving={saving} goBack={goBack} />
      <Divider />
      <Typography align="center">or</Typography>
      <Divider />
      <OnetimePayment />
    </React.Fragment>
  )
}

